@import '../../components/globalStyle.scss';

.yearList {
  display: flex;
  justify-content: space-around;
  width: 100%;
}	

.yearSelection {
  margin-bottom: toRem(40);

  :hover {
    cursor: pointer;
  }

  .yearTitle {
    font-size: $smallFontSize;
    font-weight: 300;
    margin: 0;
    padding: 0px 7px 10px 10px;


  }

  .yearTitle:hover {
    font-weight: 450;
  }

  .yearSelectedTitle {
    font-size: $smallFontSize;
    font-weight: 600;
    margin: 0;
    padding: 0px 10px 6px 10px;
    border-bottom: 4px solid #000000;
  }

}


.container{
  // size
  width: 90%;
  padding-bottom: toRem(60);
  // center
  margin: 0 auto;

  
  .newsWrapper{
    // size
    width: 100%;

    // flex
    display: flex;
    flex-wrap: wrap;
  }

}